<template>
    <el-dialog
        width="600px"
        title="角色授权"
        :visible.sync="isVisible"
        :close-on-click-modal="false"
        :close-on-press-escape="false"
         @close="cancel"
    >
      <common-wrapper style="margin:10px 0;">
          <common-table>
              <template slot="table">
                  <el-table
                      ref="roleTable"
                      height="350px"
                      stripe
                      v-loading="isListLoading"
                      :data="tableData"
                      @row-click = "rowClick">
                      <el-table-column width="55" align="center">
                        <template slot-scope="scope">
                          <el-radio class="radio" :label="scope.row.roleUuid" v-model="currentId">&nbsp</el-radio>
                        </template>
                      </el-table-column>
                      <el-table-column type="index" :index="indexMethod" label="序号" width="50"></el-table-column>
                      <el-table-column prop="roleName" label="角色名称"></el-table-column>
                      <el-table-column prop="roleEnable" label="状态" :formatter="formatStatus"></el-table-column>
                      <el-table-column prop="roleCreateTime" label="创建时间" :formatter="formatCreateDate"></el-table-column>
                  </el-table>
              </template>
              <template slot="pagination">
                  <el-pagination   background   layout="total, prev, pager, next"
                  :current-page.sync="pagination.pageNum"
                  :page-size="pagination.pageSize"
                  :total="pagination.total"
                  @current-change="changePage"
                  ></el-pagination>
              </template>
          </common-table>
      </common-wrapper>
      <el-alert title="您勾选的角色信息将覆盖账号原角色信息." type="warning" show-icon :closable="false"></el-alert>
      <template slot="footer">
          <el-button type="primary" @click="save" :disabled="!selectRow.length">保存</el-button>
          <el-button @click="cancel">取消</el-button>
      </template>
    </el-dialog>
</template>
<script>
/**
 * User Role Authorization Component
 * ----------------------
 * Author: zhou qi
 * Date: 2018.03.20
 */
import moment from 'moment'
import CommonWrapper from '@/components/commonWrapper'
import CommonTable from '@/components/commonTable'

// api
import { setUserRole } from '@/api/user'
import { getRoleList } from '@/api/role'

export default {
  name: 'UserRoleAuth',
  data () {
    return {
      isVisible: false,
      tableData: [],
      currentId: '',
      selectRow: {}, // 列表选中列
      isListLoading: false,
      pagination: { // 翻页数据
        pageNum: 1,
        pageSize: 10,
        total: 0
      }
    }
  },
  props: {
    show: {
      type: Boolean,
      required: true
    },
    selectedUsers: {
      type: Object,
      required: true
    }
  },
  watch: {
    show (isShow) {
      if (isShow) {
        this.queryList({userUuid: this.selectedUsers.userUuid})
      }
      this.isVisible = isShow
    }
  },
  methods: {
    // 查询和初始化查询
    // 查询/翻页公用方法
    queryList (params) {
      const searchParams = this.searchParams = params
      this.getRoles(
        {
          ...searchParams,
          pageNum: this.pagination.pageNum,
          pageSize: this.pagination.pageSize
        }
      )
    },
    // 获取列表
    getRoles (params) {
      this.isListLoading = true
      getRoleList(params).then(res => {
        this.tableData = res.item
        this.pagination.total = res.total
        this.pagination.pageNum = res.pageNum
        this.pagination.pageSize = res.pageSize
        this.isListLoading = false
        this.tableData.forEach(row => {
          if(row.checked){
            this.currentId = row.roleUuid;
          }
        });
      })
    },
    // 列表翻页
    changePage (pageNum) {
      this.pagination.pageNum = pageNum
      this.queryList(this.searchParams)
    },
    // 生成列表序号
    indexMethod (index) {
      return (index + 1) + (this.pagination.pageNum - 1) * this.pagination.pageSize
    },
    // 列表状态显示格式化
    formatStatus (row, column) {
      return row.roleEnable === 1 ? '正常' : row.roleEnable === 0 ? '禁用' : '未知'
    },
    // 列表日期格式化
    formatCreateDate (row) {
      return moment(row.roleCreateTime).format('YYYY-MM-DD')
    },
    rowClick (row) {
      this.currentId = row.roleUuid;
    },
    save () {
      const userRoleUserUuid = this.selectedUsers.userUuid
      const userRoleRoleUuid = this.currentId
      setUserRole({userRoleUserUuid, userRoleRoleUuid}).then(res => {
        if(!res.error){
          this.$message({
            message: '用户授权成功!',
            type: 'success'
          })
          this.cancel()
          this.$emit('updateList')
        }
      })
    },
    cancel () {
      this.$emit('update:show', false)
      this.isVisible = false
    }
  },
  components: {
    CommonWrapper,
    CommonTable
  }
}
</script>



