<template>
    <div>
      <!-- 查询 -->
      <common-wrapper>
        <user-management-search class="search" :org="orgnization" @search="queryList" @rest="rest" />
      </common-wrapper>
      <!-- <header-breadcrumb :breadData="$route.matched" /> -->
      <nav-button>
        <el-button-group>
           <el-button icon="fdddfont icon-add-new" @click="handleAdd">新增用户</el-button>
          <el-button icon="fdddfont icon-shouquan" :disabled="!isButtonIsDisabled()" @click="handelRoleAuth">授权</el-button>
        </el-button-group>
      </nav-button>

     <!-- 用户列表 -->
      <common-wrapper>
        <common-table :flex="230">
          <template slot="table">
            <el-table
              ref="userTable"
              height="100%"
              stripe
              highlight-current-row
              v-loading="isListLoading"
              :data="tableData"
              @row-click="getUserDetail"
              @current-change="selected => selectRow = selected">
                <el-table-column type="current" width="30" ></el-table-column>
                <el-table-column type="index" :index="indexMethod" label="序号" width="50"></el-table-column>
                <el-table-column prop="userAccount" label="用户名"></el-table-column>
                <el-table-column prop="userRealName" label="真实姓名"></el-table-column>
                <el-table-column prop="userGender" label="性别" :formatter="formatSex" width="60" ></el-table-column>
              <el-table-column prop="userTelephone" label="联系电话"></el-table-column>
                <el-table-column prop="userOrgName" label="仓库"></el-table-column>
               <el-table-column prop="roleName" label="角色"></el-table-column>
              <el-table-column prop="userCompany" label="所属公司"></el-table-column>
                <el-table-column label="操作" width="190" >
                  <template slot-scope="scope">
                    <el-button size="mini" round icon="fdddfont icon-modify" @click.stop="handleEdit(scope.$index, scope.row)"></el-button>
                    <el-button size="mini" round icon="fdddfont icon-delete"   @click.stop="handleDel(scope.$index, scope.row)"></el-button>
                  </template>
                </el-table-column>
            </el-table>
          </template>
          <template slot="pagination">
                <el-pagination   background   layout="total, prev, pager, next"
                :current-page.sync="pagination.pageNum"
                :page-size="pagination.pageSize"
                :total="pagination.total"
                @current-change="changePage"
                ></el-pagination>
          </template>
        </common-table>
      </common-wrapper>

     <!-- 新增、修改用户, 因二者格式不同,故分开两个组件调用 -->
     <user-create
       :show.sync="isCreateFormVisible"
       :org="orgnization"
       @updateList="queryList"
     />
     <user-edit
        :show.sync="isEditFormVisible"
        :org="orgnization"
        :modifyUser.sync="modifyUser"
        @updateList="queryList"
      />

      <!-- 角色授权 -->
      <user-role-auth
        :selectedUsers="selectRow"
        :show.sync="isRoleAuthVisible"
        @updateList="queryList"
      />
    </div>
</template>

<script>
/**
 * User Management Module
 * ----------------------
 * Author: zhou qi
 * Date: 2018.03.20
 */
import moment from 'moment'
// 全局混入
import {mixins} from '@/mixins'
// 自定义组件
import NavButton from '@/components/navButton'
import NavPremissonButton from '@/components/navButton/navPremissonButton'
import CommonWrapper from '@/components/commonWrapper'
import CommonTable from '@/components/commonTable'
import UserManagementSearch from './Components/Search'
import UserCreate from './Components/UserCreate'
import UserEdit from './Components/UserEdit'
import UserRoleAuth from './Components/UserRoleAuth'
// API
import { getAllOrgList } from '@/api/common'
import { getUsersList, deleteUser, resetPassword } from '@/api/user'

export default {
  name: 'UserManagement',
  mixins: [mixins],
  data () {
    return {
      comp: this,
      buttonData: [],
      tableData: [],
      selectRow: {}, // 列表选中列
      isListLoading: false, // 列表加载
      orgnization: [], // 公用组织机构
      isCreateFormVisible: false, // 新建界面是否显示
      isEditFormVisible: false, // 编辑界面是否显示
      isRoleAuthVisible: false, // 用户角色授权窗口是否展示
      modifyUser: null, // 弹出框编辑时存放的临时行数据
      pagination: { // 翻页数据
        pageNum: 1,
        pageSize: 10,
        total: 0
      },
      searchParams: {} // 查询参数
    }
  },
  activated () {
    this.$refs.userTable.doLayout()
  },
  mounted () {
    //
    getAllOrgList().then(res => {
      this.orgnization = res
    })
  },
  methods: {
    // 查询和初始化查询
    // 查询/翻页公用方法
    queryList (params) {
      const searchParams = this.searchParams = params
      this.getUsers(
        {
          ...searchParams,
          pageNum: this.pagination.pageNum,
          pageSize: this.pagination.pageSize
        }
      )
    },
    // 获取用户列表
    getUsers (params) {
      this.isListLoading = true
      getUsersList(params).then(res => {
        this.tableData = res.item
        this.pagination.total = res.total
        this.pagination.pageNum = res.pageNum
        this.pagination.pageSize = res.pageSize
        this.isListLoading = false
      })
    },
    rest (params) {
      const searchParams = this.searchParams = params
      this.getUsers(
        {
          ...searchParams,
          pageNum: 1,
          pageSize: this.pagination.pageSize
        }
      )
    },
    formatSex (row) {
      return row.userGender == 1 ? '男' : row.userGender == 0 ? '女' : '未知';
    },
    isButtonIsDisabled () {
      return !!this.selectRow.userUuid
    },
    // 显示编辑界面
    handleEdit (index, row) {
      this.modifyUser = row
      this.isEditFormVisible = true
    },
    // 显示新增界面
    handleAdd () {
      this.isCreateFormVisible = true
    },
    handelRoleAuth () {
      // if (!this.selectRow.length) {
      //   this.$message({
      //     type: 'warning'

      //   })
      // }
      this.isRoleAuthVisible = true
    },
    isButtonDisabled () {
      return this.selectRow.userUuid
    },
    // 重置密码
    resetPassword (index, row) {
      this.$confirm(`确认要重置 ${row.userRealName} 的密码吗?`, '提示', {
        type: 'warning'
      }).then(() => {
        let params = {
          userId: row.userUuid
        }
        resetPassword(params).then((res) => {
          if (!res.error) {
            this.$message({
              message: '用户密码重置成功',
              type: 'success'
            })
            this.queryList()
          }
        })
      }).catch(() => {

      })
    },
    // 查询详情
    getUserDetail (row, event) {
      // console.log(row)
    },
    // 单行删除
    handleDel (index, row) {
      this.$confirm('确认删除该用户吗?', '提示', {
        type: 'warning'
      }).then(() => {
        let params = {
          userUuid: row.userUuid
        }
        this.deleteUsers(params)
      }).catch(() => {})
    },
    // 批量删除
    batchRemove () {
      this.$confirm('确认删除选中的用户吗？', '提示', {
        type: 'warning'
      }).then(() => {
        let userIds = []
        this.selectRow.map(row => userIds.push(row.userUuid))
        this.isListLoading = true
        this.deleteUsers(userIds).then(() => {
          this.isListLoading = false
        })
      }).catch(() => {})
    },
    // 删除接口方法
    deleteUsers (userIds) {
      return deleteUser(userIds).then((res) => {
        this.$message({
          message: '删除成功',
          type: 'success'
        })
        this.queryList()
      })
    },
    // 列表翻页
    changePage (pageNum) {
      this.pagination.pageNum = pageNum
      this.queryList(this.searchParams)
    },
    // 生成列表序号
    indexMethod (index) {
      return (index + 1) + (this.pagination.pageNum - 1) * this.pagination.pageSize
    },
    // 列表状态显示格式化
    formatStatus (row, column) {
      return row.userEnable === '1' ? '启用' : row.userEnable === '0' ? '禁用' : '未知'
    },
    // 列表日期格式化
    formatCreateDate (row) {
      return moment(row.userCreateTime).format('YYYY-MM-DD')
    }
  },
  components: {
    NavButton,
    NavPremissonButton,
    CommonWrapper,
    CommonTable,
    UserManagementSearch,
    UserCreate,
    UserEdit,
    UserRoleAuth
  }
}
</script>



